.highlightSection{
    position: relative;
    overflow: hidden;
    width: 100%;
    height: 100vh;
    box-sizing: border-box;

    .content{
        width: 100%;
        height: 100%;
        box-sizing: border-box;
    
        .bgBox{
            position: absolute;
            left: 0;
            top: 0;
            z-index: -1;
            width: 100vw;
            height: 100vh;
            opacity: .5;
            overflow:hidden;
            video{
                width: 100%;
                -o-object-fit: cover;
                object-fit: cover;
            }
        }
        .textBox{
            width: 100%;
            height: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;
            position: relative;
            font-size: 5.7292vw; // 110
            line-height: 6.7708vw; // 130
            font-weight: bold;
            p{
                @include engFont;
            }
        }
    }
}

@include tablet{
    .highlightSection{
        .textBox{
            font-size: 7.8125vw; // 110
            line-height: 9.3750vw; // 130
        }
    }
}

@include mobile{
    .highlightSection{
        .textBox{
            font-size: 10.4167vw; // 110
            line-height: 13.0208vw; // 130
        }
    }
}